import { postAplicationJSON } from '@/utils/request';

// 设备实时产量统计
export const getAllGroupRealProductionStatistics = (param, jsonData) =>
  postAplicationJSON(
    'webProductionStatistics/getAllGroupRealProductionStatistics.action?',
    param,
    jsonData
  );

