<template>
  <el-main>
    <div class="searchDiv">
      <div>
        <span>{{EQUIPMENT_MONITORING.DATE_RANGE}}:</span>
        <el-date-picker
          class="dateTime"
          v-model="dateTime"
          type="datetimerange"
          size='mini'
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          format="yyyy-MM-dd HH:mm:ss"
          value-format="yyyy-MM-dd HH:mm:ss"
          :picker-options='pickerOptions'
        >
        </el-date-picker>
         <span>班次范围</span>
          <el-select v-model="shiftData"  size="mini" placeholder="请选择">
            <el-option
              v-for="item in shiftOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
      </div>
      <el-button
        @click="search"
        size='mini'
        type="primary"
      >{{BTN_TEXT.SEARCH}}</el-button>
      <!-- <el-button
        @click="reset"
        size='mini'
      >{{BTN_TEXT.RESET}}</el-button> -->
    </div>
    <VXETable
      id="table"
      :height="$options.filters.getTableHeight(280)"
      :tableName="''"
      :tableLoading="tableLoadings"
      :tableDatas="tableDatas"
      :tableColumns="tableColumns"
      :tablePage="tablePages"
      @onTablePageChange="onTablePageChanges"
      :edit-config="editConfig"
      ref="xGrid"
    ></VXETable>
  </el-main>
</template>

<script>
// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
// import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EQUIPMENT_MONITORING
} from '@/utils/constantsFile';
import { searchCondetionFunc } from '@/utils/publicFunction';
// 导入所需接口
import { getAllGroupProductionStatistics } from '@/api/lineProduct';
// import { exportWarningStatistics } from '@/api/export';
// import { CommonService } from '@/services/common.service';
import { configurationGetConfigurationByEnum } from '@/api/statusEquipmentReport.js';
export default {
  components: {
    // 加载通用table组件
    VXETable
  },
  props: {
    type: Number,
    conditionArr1: {
      type: Array,
      default: function() {
        return [];
      }
    },
    conditionArr2: {
      type: Array,
      default: function() {
        return [];
      }
    },
    savedSearchCondition: {
      type: Array,
      default: function() {
        return [];
      }
    },
    groupOperator: Number,
    currenType: String
  },
  data() {
    return {
      EQUIPMENT_MONITORING: EQUIPMENT_MONITORING,
      BTN_TEXT: BTN_TEXT,
      dateTime: '',
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      // 显示那个表
      showTable: true,
      // table是否加载
      tableLoadings: false,
      // 打印数据
      outData: [],
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'groupName',
          title: '分组名称',
          width: '',
          align: 'center'
        },
        {
          field: 'recordValue',
          title: this.$store.state.pageInfo.pageConfigInfo.yieldKey,
          width: '',
          align: 'center'
        }
      ],

      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      // 班次列表
      shiftOptions: [],
      // 班次值
      shiftData: null
    };
  },
  watch: {

  },
  created() {
    this.getSystemShift();
    this.dateTime = [`${this.moment(new Date()).format('YYYY-MM-DD')} 00:00:00`, `${this.moment(new Date()).format('YYYY-MM-DD')} 23:59:59`];

    this.findList();
  },
  methods: {
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      // var searchDatax = {};
      // if (this.conditionArr1.length > 0 || this.conditionArr2.length > 0) {
      //   searchDatax = CommonService.generateSearchConditionJSON(
      //     this.conditionArr1,
      //     this.operatorGroup,
      //     this.conditionArr2
      //   );
      // }
      var conditionJson = {
        conditionJson1: [],
        operatorGroup: 1,
        conditionJson2: []
      };
      if (this.savedSearchCondition.length > 0) {
        conditionJson = searchCondetionFunc(...this.savedSearchCondition);
      }

      if (!this.dateTime) {
        this.dateTime = [`${this.moment(new Date()).format('YYYY-MM-DD')} 00:00:00`, `${this.moment(new Date()).format('YYYY-MM-DD')} 23:59:59`];
      }

      var startTime = this.dateTime[0];
      var endTime = this.dateTime[1];

      var param = {
        size: this.tablePages.pageSize,
        current: this.tablePages.currentPage,
        startTime: startTime,
        endTime: endTime,
        deviceGroupTypeEnum: this.currenType
      };

      if (this.shiftData !== null) {
        // eslint-disable-next-line no-unused-expressions
        param.shiftStart = this.shiftOptions[this.shiftData].shiftStart;
        param.shiftEnd = this.shiftOptions[this.shiftData].shiftEnd;
      }

      try {
        this.tableLoadings = true;
        const res = await getAllGroupProductionStatistics(param, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;
          // 将总条数写入table分页配置
          this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 查询
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    search() {
      this.onTablePageChanges(1, 10);
    },
    /**
     * @description: 重置
     * @param {type}
     * @return:
     * @author: CH
     */
    reset() {
      this.groupId = '';
      this.lineId = '';
      this.deviceName = '';
      this.dateTime = '';
      this.onTablePageChanges(1, 10);
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      // var searchDatax = {};
      // if (this.conditionArr1.length > 0 || this.conditionArr2.length > 0) {
      //   searchDatax = CommonService.generateSearchConditionJSON(
      //     this.conditionArr1,
      //     this.operatorGroup,
      //     this.conditionArr2
      //   );
      // }
      var conditionJson = {
        conditionJson1: [],
        operatorGroup: 1,
        conditionJson2: []
      };
      if (this.savedSearchCondition.length > 0) {
        conditionJson = searchCondetionFunc(...this.savedSearchCondition);
      }

      if (!this.dateTime) {
        this.dateTime = [`${this.moment(new Date()).format('YYYY-MM-DD')} 00:00:00`, `${this.moment(new Date()).format('YYYY-MM-DD')} 23:59:59`];
      }

      var startTime = this.dateTime[0];
      var endTime = this.dateTime[1];

      var param = {
        size: 99999999,
        current: this.tablePages.currentPage,
        startTime: startTime,
        endTime: endTime,
        deviceGroupTypeEnum: this.currenType
      };

      if (this.shiftData !== null) {
        // eslint-disable-next-line no-unused-expressions
        param.shiftStart = this.shiftOptions[this.shiftData].shiftStart;
        param.shiftEnd = this.shiftOptions[this.shiftData].shiftEnd;
      }

      try {
        this.tableLoadings = true;
        const res = await getAllGroupProductionStatistics(param, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          // this.tableDatas = res.rows;
          // 将总条数写入table分页配置
          // this.tablePages.total = res.total;
          this.$refs.xGrid.exportDatax(this.$store.state.pageInfo.pageConfigInfo.yieldKey + '报表【历史总计】', res.rows);
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }

      // this.tableLoadings = true;
      // var Id = '';
      // if (this.lineId !== '') {
      //   Id = this.lineId;
      // } else {
      //   Id = this.groupId;
      // }
      // var startTime = '';
      // var endTime = '';
      // if (this.dateTime[0] !== undefined) {
      //   startTime = this.dateTime[0] + ' 00:00:00';
      //   endTime = this.dateTime[1] + ' 23:59:59';
      // }

      // try {
      //   const params = {
      //     size: this.tablePages.pageSize,
      //     current: this.tablePages.currentPage,
      //     groupId: Id,
      //     deviceName: this.deviceName,
      //     startTime: startTime,
      //     endTime: endTime
      //   };
      //   const res = await exportWarningStatistics(params);
      //   if (res.data.code !== undefined) {
      //     showWarningMessage(res.message);
      //     this.tableLoadings = false;
      //   } else {
      //     var fileName = '';
      //     fileName = '告警统计.xls';
      //     this.tableLoadings = false;
      //     this.$options.filters.setExportFunc(res, fileName);
      //   }
      // } catch (error) {
      //   showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      // } finally {
      // }
    },
    async getSystemShift() {
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'SHIFT_TIME'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          if (data.rows.processConfiguration) {
            var jsonArr = JSON.parse(data.rows.processConfiguration);
            this.shiftOptions = [];
            this.shiftData = null;
            var index = 0;
            for (var key in jsonArr) {
              var json = {
                value: index,
                label: key + ' ' + jsonArr[key],
                shiftStart: jsonArr[key].split('-')[0],
                shiftEnd: jsonArr[key].split('-')[1]
              };
              ++index;
              this.shiftOptions.push(json);
            }
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  }
};

</script>

