        const res = await getAllGroupRealProductionStatistics({
          size: this.tablePages.pageSize,
          current: this.tablePages.currentPage,
          deviceGroupTypeEnum: this.currenType
        },
        conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;
          // 将总条数写入table分页配置
          this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

